.search-input {
  border: 1px solid #e9e9e9;
  padding: 5px;
}
.color-table tr {
 margin-bottom: 10px;
 display: block;
}
.color-table th {
  text-align: left;
  font-size: 20px;
}
.color-table td:nth-of-type(1),
.color-table th:nth-of-type(1) {
  width: 100px;
  height: 50px;
}
.color-table td:nth-of-type(2),
.color-table th:nth-of-type(2) {
  width: 220px;
  height: 50px;
}
.color-table td + td,
.color-table th + th {
  padding-left: 20px;
  min-width: 180px;
}
